
import React from "react";
import { HashRouter as Router,Route, Switch } from 'react-router-dom';
import About from './components/pages/About';
import Portfolio from './components/pages/Portfolio';
import Resume from './components/pages/Resume';
import Contact from './components/pages/Contact';
import Nav from './components/NavBar';
import Footer from './components/Footer';

import './App.css';

function App() {
    return(
        
            <div>
                <Nav/>
                <Router >
                    <Switch>
                        <Route exact path = '/' component={About}/>
                        <Route exact path = 'professional-portfolio-react/#/portfolio' component={Portfolio}/>
                        <Route exact path = 'professional-portfolio-react/#/resume' component={Resume}/>
                        <Route exact path = 'professional-portfolio-react/#/contact' component={Contact}/>
                    </Switch>
                </Router>
                <Footer/>
            </div>
        
    );
}


export default App;
